import React, { Component, useState, useRef } from 'react'
import { compose, withProps, withStateHandlers } from 'recompose'
import {
    Marker,
    GoogleMap,
    withScriptjs,
    withGoogleMap,
    InfoWindow
} from 'react-google-maps'
import { MarkerClusterer } from 'react-google-maps/lib/components/addons/MarkerClusterer'
import SearchBox from './SearchBox'
import { actionFetchEventsRequest } from '../action/actions'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom';


const Map = compose(
    withStateHandlers(() => ({
        isOpen: false,
        markerIndex: 0
    }), {
        onToggleOpen: ({ isOpen }) => (index) => ({
            isOpen: true,
            markerIndex: index
        })
    }),
    withScriptjs,
    withGoogleMap
)(props => {
    console.log(props);
    const mapRef = useRef(null)
    const [zoom, setZoom] = useState(12)
    const [marker, setMarker] = useState({ hasMarker: false, position: {} })
    const [center, setCenter] = useState({ lat: 10.807279, lng: 106.630638 })
    const handlePlacesChanged = place => {
        setZoom(12)
        setCenter({
            lat: place.geometry.location.lat(),
            lng: place.geometry.location.lng(),
        })
        setMarker({
            hasMarker: true,
            position: {
                lat: place.geometry.location.lat(),
                lng: place.geometry.location.lng(),
            },
        })
    }
    return (
        <GoogleMap
            zoom={zoom}
            center={center}
        >
            <SearchBox onPlacesChanged={handlePlacesChanged} />
            {marker.hasMarker}

            <MarkerClusterer
                averageCenter
                enableRetinaIcons
                gridSize={30}
            >
                {props.events.events.map((point, index) => (
                    <Marker key={index}
                        position={{ lat: parseFloat(point.cs_lat), lng: parseFloat(point.cs_long) }}
                        onClick={() => { props.onToggleOpen(index) }}
                    >
                        {props.isOpen && props.markerIndex === index &&
                            <InfoWindow onCloseClick={props.onToggleOpen}>
                                <div className="infoWindow"  >
                                    <h5>{point.cs_name}</h5>
                                    <p style={{ fontSize: "16px" }}>{point.cs_address}</p>
                                    {/* <a style={{ fontSize: "16px" }} > */}
                                        <Link to={`/event-detail/${point.clean_site_id}`}>View</Link>
                                        {/* </a> */}
                                </div>
                            </InfoWindow>
                        }
                    </Marker>
                ))}
            </MarkerClusterer>
        </GoogleMap>
    )
}
);





class MapUI extends Component {

    constructor() {
        super();
        this.state = {
            isOpen: false,
            events: []
        }
        this.onToggleOpen = this.onToggleOpen.bind(this);
    }

    onToggleOpen() {
        this.setState({
            isOpen: !this.state.isOpen
        });
    }


    componentDidMount() {
        this.props.actionFetchEvents();
        console.log(this.props.events, "test")
    }

    render() {
        console.log(this.props.events, "zzz")
        return (
            <div className="App" style={{ backgroundColor: "rgb(250, 250, 250)" }}>
                <br/><br/><br/><br/><br/>
                {/* <button className="nav-link navFontColor"
                                        style={{ backgroundColor: "rgb(250, 250, 250)" }}
                                        onClick={localStorage.removeItem('usertoken')} href='/#/'>
                                        Sign Out
                            </button> */}
                <div className="pageTitle">Clean Up Events Around You</div>
                <br/>
                <div className="container" >
                    <Map
                        events= {this.props.events}
                        defaultOptions={{ scaleControl: true }}
                        googleMapURL={`https://maps.googleapis.com/maps/api/js?key=AIzaSyBt5F_65miIiCpSgOEmbg3hACbgU0RwZas&libraries=visualization,drawing,geometry,places`}
                        loadingElement={<div style={{ height: '100%' }}> Loading... </div>}
                        containerElement={<div style={{ height: '75vh' }} />}
                        mapElement={<div style={{ height: '100%' }} />}
                    />
                </div>
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        // businesses: state.businesses
        events: state.events
    }
}
const mapDispatchToProps = (dispatch, props) => {
    return {
        actionFetchEvents: () => { dispatch(actionFetchEventsRequest()) },
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(MapUI)