import axios from 'axios'

export const register = newUser => {
  return axios
    .post('http://cleanupvn.ap-southeast-1.elasticbeanstalk.com/users/register', {
      acc_email: newUser.acc_email,
      acc_username: newUser.acc_username,
      acc_pass: newUser.acc_pass,
    })
    .then(response => {
      console.log('Registered', response.data)
    })
}

export const login = user => {
  return axios
    .post('http://cleanupvn.ap-southeast-1.elasticbeanstalk.com/users/login', {
      acc_email: user.acc_email,
      acc_pass: user.acc_pass
    })
    .then(response => {
      localStorage.setItem('usertoken', response.data)
      return response.data
    })
    .catch(err => {
      console.log(err)
    })
}
