import React, { Component } from 'react'
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import './../style.css'
import withStyles from "@material-ui/core/styles/withStyles";
import { register } from '../action/UserFunctions'
import { Modal, ModalHeader, ModalBody } from 'reactstrap'

const validEmailRegex = RegExp(/^(([^<>()\[\]\.,;:\s@\"]+(\.[^<>()\[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i);


const styles = {
  paper: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  form: {
    width: '100%', // Fix IE 11 issue.
  },
}


class SignUp extends Component {
  constructor() {
    super()
    this.state = {
      acc_username: '',
      acc_email: '',
      acc_pass: '',
      errors: {},

      notify: false,
      usernameErr: false,
      emailErr: false,
      passErr: false,
    }
    this.handleEmailChange = this.handleEmailChange.bind(this)
    this.handleUsernameChange = this.handleUsernameChange.bind(this)
    this.handlePasswordChange = this.handlePasswordChange.bind(this)
    this.onSubmit = this.onSubmit.bind(this)
  }

  onSubmit(e) {
    e.preventDefault()
    const newUser = {
      acc_email: this.state.acc_email,
      acc_pass: this.state.acc_pass,
      acc_username: this.state.acc_username,
    }
    if (this.state.acc_username === "") {
      this.setState({
        emailErr: false,
        passErr: false,
        usernameErr: true
      })
      
    } else if (this.state.acc_email === "" || validEmailRegex.test(this.state.acc_email) === false ) {
        this.setState({
          emailErr: true,
          passErr: false,
          usernameErr: false
        })
    } else if (this.state.acc_pass === "") {
        this.setState({
          emailErr: false,
          passErr: true,
          usernameErr: false
      })
    } else {
      this.setState({
        emailErr: false,
        passErr: false,
        usernameErr: false
    })
      register(newUser).then(res => {
        this.props.history.push(`/`)
      })
  
      this.setState({ notify: true })
    }
    }

    

  handleUsernameChange(e) {
    this.setState({
      acc_username: e.target.value
    });
  }

  handleEmailChange(e) {
    this.setState({
      acc_email: e.target.value
    });
  }

  handlePasswordChange(e) {
    this.setState({
      acc_pass: e.target.value
    });
  }

  render() {
    const classes = this.props;
    return (
      <Container component="main" maxWidth="xs" className="Min-height">
        <br /><br /><br /><br /><br />
        <Modal isOpen={this.state.notify}>
          <ModalBody><h5 style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>You've successfully created your account!</h5></ModalBody>
          <ModalBody><h5 style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>You will be redirected to Sign In page.</h5></ModalBody>
          <ModalBody>
            <Link to={`/`} style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
              <button className="btn btn-success btn-sm">
                Xác Nhận
                                </button>
            </Link>
          </ModalBody>
        </Modal>
        <CssBaseline />
        <div className={classes.paper}>
          <Typography className="signIn" component="h1" variant="h5">
            Sign Up
          </Typography>
          <form className={classes.form} onSubmit={this.onSubmit} noValidate>
            <TextField
              autoComplete="fname"
              name="firstName"
              variant="outlined"
              required
              margin="normal"
              fullWidth
              id="username"
              label="Username"
              autoFocus
              value={this.state.acc_username}
              onChange={this.handleUsernameChange}
            />
            {this.state.usernameErr && 
              <span className='error'>Please enter your username!</span>}
            <TextField
              variant="outlined"
              required
              margin="normal"
              fullWidth
              id="email"
              label="Email Address"
              name="email"
              autoComplete="email"
              value={this.state.acc_email}
              onChange={this.handleEmailChange}
            />
            {this.state.emailErr && 
              <span className='error'>Please enter a valid email!</span>}
            <TextField
              variant="outlined"
              required
              margin="normal"
              fullWidth
              name="password"
              label="Password"
              type="password"
              id="password"
              autoComplete="current-password"
              value={this.state.acc_pass}
              onChange={this.handlePasswordChange}
            />
            {this.state.passErr && 
              <span className='error'>Please enter your password!</span>}
            <br />
            <br />
            <br />
            <button className="btn noHoverButton  btn-block" type="submit" on>Đăng Kí</button>
            <br />
            <Grid container justify="flex-end">
              <Grid item>
                <Link href="#/sign-in" variant="body2">
                  Already have an account? Sign In !
                </Link>
              </Grid>
            </Grid>
          </form>
        </div>
        <Box mt={5}>
        </Box>
      </Container>
    );

  }

}
export default withStyles(styles)(SignUp); 